<template>
  <div class="rank">
    <div class="rank-list" ref="rankWraper">
      <ul>
        <li class="item" v-for="item in topList" @click="selectItem(item)">
          <div class="icon">
            <img v-lazy="item.picUrl" width="100" height="100" />
          </div>
          <ul class="songlist">
            <li class="song" v-for="(song,index) in item.songList">
              <span>{{index+1}}</span>
              <span>{{song.songname}}-{{song.singername}}</span>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
  import {getRankList} from 'api/rank'
  import {ERROR_OK} from 'api/config'
  import BScroll from 'better-scroll'

  export default {
      data(){
        return{
          topList:[]
        }
      },
      created(){
          this._getRankList()
      },
      methods:{
        _getRankList(){
          getRankList().then((res)=>{
            if(res.code === ERROR_OK){
                this.topList = res.data.topList
                this.$nextTick(()=>{
                  this._initScroll()
                })
            }
          })
        },
        selectItem(item){
          this.$router.push({
            path : `/rank/${item.id}`
          })
        },
        _initScroll(){ //初始化滚动条
          this.rankScroll = new BScroll(this.$refs.rankWraper, {
            'click':true
          })
        }
      }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  .rank
    .rank-list
      position:fixed
      width:100%
      top: 88px
      bottom:0
      overflow:hidden
      .item
        display:flex
        margin:0 20px
        padding-top:20px
        height:100px
        .icon
          flex: 0 0 100px
          width:100px
          height:100px
        .songlist
          flex:1
          padding:0 20px
          height:100px
          display:flex
          flex-direction:column
          justify-content:center
          //background: $color-highlight-background
          //color: $color-text-d
          font-size: $font-size-small
          .song
            no-wrap()
            line-height: 26px
</style>



